import Navbar from '@/components/Navbar'
import Footer from '@/components/Footer'
import Image from 'next/image'
import Link from 'next/link'

// Sample blog posts - would normally come from database
const blogPosts = [
  {
    id: '1',
    slug: 'spring-planting-guide',
    title: 'Your Complete Spring Planting Guide',
    excerpt: 'Discover the best vegetables and herbs to plant this spring, along with expert tips for a successful growing season.',
    author: 'Jane Smith',
    category: 'Gardening Tips',
    image: '/images/bg.jpeg',
    publishedAt: '2024-02-15',
    tags: ['spring', 'planting', 'vegetables'],
  },
  {
    id: '2',
    slug: 'composting-basics',
    title: 'Composting Basics: Turn Waste into Garden Gold',
    excerpt: 'Learn how to create nutrient-rich compost from kitchen scraps and yard waste with our comprehensive guide.',
    author: 'John Doe',
    category: 'Sustainability',
    image: '/images/bg.jpeg',
    publishedAt: '2024-02-10',
    tags: ['composting', 'sustainability', 'organic'],
  },
  {
    id: '3',
    slug: 'urban-gardening-tips',
    title: '10 Tips for Successful Urban Gardening',
    excerpt: 'Maximize your small space with these proven strategies for growing food in the city.',
    author: 'Sarah Green',
    category: 'Urban Farming',
    image: '/images/bg.jpeg',
    publishedAt: '2024-02-05',
    tags: ['urban', 'small-space', 'balcony'],
  },
  {
    id: '4',
    slug: 'heirloom-seeds',
    title: 'The Importance of Heirloom Seeds',
    excerpt: 'Why heirloom varieties matter and how you can help preserve agricultural biodiversity.',
    author: 'Jane Smith',
    category: 'Education',
    image: '/images/bg.jpeg',
    publishedAt: '2024-01-28',
    tags: ['seeds', 'heritage', 'preservation'],
  },
  {
    id: '5',
    slug: 'winter-gardening',
    title: 'Winter Gardening: Yes, It\'s Possible!',
    excerpt: 'Don\'t let cold weather stop you. Learn which crops thrive in winter and how to protect your plants.',
    author: 'John Doe',
    category: 'Seasonal',
    image: '/images/bg.jpeg',
    publishedAt: '2024-01-20',
    tags: ['winter', 'cold-weather', 'season-extension'],
  },
  {
    id: '6',
    slug: 'community-garden-success',
    title: 'Building a Thriving Community Garden',
    excerpt: 'Tips and best practices for creating and maintaining a successful community garden space.',
    author: 'Sarah Green',
    category: 'Community',
    image: '/images/bg.jpeg',
    publishedAt: '2024-01-15',
    tags: ['community', 'collaboration', 'organization'],
  },
]

export default function BlogPage() {
  return (
    <main>
      <Navbar />

      {/* Hero Section */}
      <section className="relative h-96 flex items-center justify-center">
        <div className="absolute inset-0 z-0">
          <Image
            src="/images/bg.jpeg"
            alt="Blog Background"
            fill
            className="object-cover"
          />
          <div className="absolute inset-0 bg-black opacity-50"></div>
        </div>
        <div className="relative z-10 text-center text-white px-4">
          <h1 className="text-5xl md:text-6xl font-bold mb-4">Our Blog</h1>
          <p className="text-xl md:text-2xl">
            Gardening tips, sustainability insights, and community stories
          </p>
        </div>
      </section>

      {/* Blog Posts Grid */}
      <section className="py-16 bg-gray-50">
        <div className="container-custom">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {blogPosts.map((post) => (
              <article
                key={post.id}
                className="bg-white rounded-lg shadow-lg overflow-hidden hover:shadow-xl transition-shadow"
              >
                <Link href={`/blog/${post.slug}`}>
                  <div className="relative h-48">
                    <Image
                      src={post.image}
                      alt={post.title}
                      fill
                      className="object-cover"
                    />
                  </div>
                </Link>
                <div className="p-6">
                  <div className="flex items-center justify-between mb-3">
                    <span className="text-sm text-secondary font-semibold">
                      {post.category}
                    </span>
                    <span className="text-sm text-gray-500">
                      {new Date(post.publishedAt).toLocaleDateString('en-US', {
                        month: 'short',
                        day: 'numeric',
                        year: 'numeric',
                      })}
                    </span>
                  </div>
                  <Link href={`/blog/${post.slug}`}>
                    <h2 className="text-2xl font-bold mb-3 text-primary hover:text-secondary transition-colors">
                      {post.title}
                    </h2>
                  </Link>
                  <p className="text-gray-600 mb-4">{post.excerpt}</p>
                  <div className="flex items-center justify-between">
                    <span className="text-sm text-gray-500">By {post.author}</span>
                    <Link
                      href={`/blog/${post.slug}`}
                      className="text-secondary hover:text-primary font-semibold"
                    >
                      Read More â†’
                    </Link>
                  </div>
                  <div className="mt-4 flex flex-wrap gap-2">
                    {post.tags.map((tag) => (
                      <span
                        key={tag}
                        className="text-xs bg-gray-100 text-gray-600 px-2 py-1 rounded"
                      >
                        #{tag}
                      </span>
                    ))}
                  </div>
                </div>
              </article>
            ))}
          </div>
        </div>
      </section>

      {/* Newsletter Signup */}
      <section className="py-16 bg-primary text-white">
        <div className="container-custom max-w-2xl text-center">
          <h2 className="text-4xl font-bold mb-4">Subscribe to Our Newsletter</h2>
          <p className="text-xl mb-8">
            Get the latest gardening tips and updates delivered to your inbox
          </p>
          <form className="flex flex-col sm:flex-row gap-4">
            <input
              type="email"
              placeholder="Enter your email"
              className="flex-1 px-6 py-3 rounded-lg text-gray-900"
              required
            />
            <button
              type="submit"
              className="bg-secondary text-white px-8 py-3 rounded-lg font-bold hover:bg-opacity-90 transition-colors"
            >
              Subscribe
            </button>
          </form>
        </div>
      </section>

      <Footer />
    </main>
  )
}
